<template>
  <div class="header-container" v-once>
    <div class="header-menu">
      <ul class="menu">
        <router-link
          v-for="item in links"
          :to="{path: item.value}"
          tag="li"
          class="menu"
          :key="item.value">
          <a class="menu">{{item.name}}</a>
          <span class="arrow" v-if="item.submenus"></span>
          <ul class="submenu" v-if="item.submenus">
            <router-link
              v-for="menu in item.submenus"
              :to="{ path: menu.value }"
              tag="li"
              class="submenu"
              :key="menu.value"
              exct>
              <a>{{menu.name}}</a>
            </router-link>
          </ul>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
import { IndexHeader } from '@/assets/js/Header'

export default {
  data () {
    return IndexHeader
  }
}
</script>

<style lang="scss" scoped>
@import '~@/assets/css/frame.scss';
.header-container{
  height: $headerHeight;
}
.header-menu{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  height: $headerHeight;
  background-color: #fff;
  border-bottom: 1px solid #e5e5e5;
  box-shadow: 0 1px 1px #e5e5e5;
  min-width: 8rem;
  ul.menu{
    float: right;
    padding-right: 0.3rem;
    li.menu{
      display: block;
      float: left;
      height: $headerHeight;
      padding: 0 0.12rem;
      line-height: $headerHeight;
      font-size: 0.16rem;
      position: relative;
      &:hover{
        ul.submenu{
          display: block;
        }
        a.menu{
          opacity: 0.8;
        }
      }
      &:hover, &.router-active{
        a.menu{
          color: $activeColor;
          padding-bottom: 0.1rem;
          border-bottom: 2px solid $activeColor;
        }
        .arrow{
          border-left-color: $activeColor;
          transform: rotate(90deg);
        }
      }
      .arrow{
        display: inline-block;
        vertical-align: middle;
        width: 0;
        height: 0;
        border-width: 5px 0 5px 5px;
        border-style: solid;
        border-color: transparent transparent transparent #333;
        transition: transform 0.1s linear;
        -webkit-will-change: transform;
      }
    }
  }
}
ul.submenu{
  position: absolute;
  right: 0.12rem;
  top: 70%;
  padding: 0.1rem 0.2rem 0.1rem 0.25rem;
  white-space: nowrap;
  background-color: #fff;
  border: 1px solid #ddd;
  box-shadow: 1px 1px 1px #ddd;
  border-radius: 3px;
  display: none;
  li.submenu{
    line-height: 2;
    font-size: 0.14rem;
    position: relative;
    &:hover, &.router-exact-active{
      a{
        color: $activeColor;
      }
    }
    &.router-exact-active{
      &:before{
        content: '';
        position: absolute;
        left: -0.15rem;
        top: 0.05rem;
        height: 0.2rem;
        width: 0.1rem;
        background: url(/static/imgs/flag.png) left no-repeat;
        background-size: contain;
      }
    }
  }
}
</style>


